import _app from '../../js/app.js';
import QStemplate from '../../template/template.vue';
import QSInputsMixin from '../../js/QSInputsMixin.js';

export default {
	components: {
		QStemplate
	},
	props: {
		color: {
			type: String,
			default: ''
		},
		scale: {
			type: [String, Number],
			default: '.8'
		},
		disabled: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		change({
			detail: {
				value
			}
		}) {
			this.setValue(value);
		}
	},
	mixins: [QSInputsMixin({
		QSInputsType: _app.typeObj.switch
	})]
};
